import React, { lazy, Suspense } from 'react';
import { Switch, Route, Redirect, useLocation } from 'react-router-dom';
import { AnimatePresence, motion } from 'framer-motion';
import { MoonLoader } from 'react-spinners';

// Layout Blueprints

import {
  LeftSidebar,
  CollapsedSidebar,
  MinimalLayout,
  PresentationLayout
} from './layout-blueprints';

import SWSiteFilterContext from './contexts/SWSiteFilterContext';
import InsightsOverviewContext from 'contexts/InsightsOverviewContext'
import InsightsDoubleClickContext from 'contexts/InsightsDoubleClickContext'
const InsightsOverview = lazy(() => import('./custom-pages/InsightsOverview'));
const InsightsDoubleClick = lazy(() => import('./custom-pages/InsightDoubleClick'));

const Routes = () => {
  const location = useLocation();

  const pageVariants = {
    initial: {
      opacity: 0,
      scale: 0.99
    },
    in: {
      opacity: 1,
      scale: 1
    },
    out: {
      opacity: 0,
      scale: 1.01
    }
  };

  const pageTransition = {
    type: 'tween',
    ease: 'anticipate',
    duration: 0.4
  };

  const SuspenseLoading = () => {
    return (
      <>
        <div className="d-flex align-items-center flex-column vh-100 justify-content-center text-center py-3">
          <div className="d-flex align-items-center flex-column px-4">
            <MoonLoader color={'#3c44b1'} loading={true} />
          </div>
          <div className="text-muted font-size-xl text-center pt-3">
            Please wait while we are loading like crazy
          </div>
        </div>
      </>
    );
  };
  return (
    <InsightsDoubleClickContext>
      <InsightsOverviewContext>
        <SWSiteFilterContext>
          <AnimatePresence>
            <Suspense fallback={<SuspenseLoading />}>
              <Switch>
                <Redirect exact from="/" to="/executiveInsights" />
                <Route
                  path={[
                    '/executiveInsights',
                    '/insightsbreakdown'
                  ]}>
                  <LeftSidebar>
                    <Switch location={location} key={location.pathname}>
                      <motion.div
                        initial="initial"
                        animate="in"
                        exit="out"
                        variants={pageVariants}
                        transition={pageTransition}>
                        <Route
                          path="/insightsbreakdown"
                          component={InsightsDoubleClick}
                        />
                        <Route
                          path="/executiveInsights"
                          component={InsightsOverview}
                        />
                      </motion.div>
                    </Switch>
                  </LeftSidebar>
                </Route>

              </Switch>
            </Suspense>
          </AnimatePresence>
        </SWSiteFilterContext>
      </InsightsOverviewContext>
    </InsightsDoubleClickContext>
  );
};

export default Routes;
