import React,{useState, createContext} from 'react';

export const InsightsDoubleClickContext = createContext();
export default function InsightsDoubleClickContextProvider(props) {
    const [sentimentProfile, setSentimentProfile] = useState({
        positive:0,
        negative:0,
        neutral:0
    });

    return (
        <InsightsDoubleClickContext.Provider value={{
            sentimentProfile, 
            setSentimentProfile

        }}>
                {props.children}
        </InsightsDoubleClickContext.Provider >
    )
}
